

.JoinPage{
    background-color: rgb(31,29,29);
    width: 100vw;
    height: 100vh;
    display: flex;
    justify-content: center;
    align-items: center;
}
.JoinContainer{
    width: 50%;
    display: flex;
    flex-direction: column;
    align-items: center;
}
.JoinContainer > img{
    width: 10vmax;
}
.JoinContainer > h1{
    font-size: 2.5vmax;
    color: white;
    font-family: 'Roboto', sans-serif;
    width: 25vmax;
    border-bottom: 0.2vmax solid white;
    text-align: center;
    padding: 1vmax;
    box-sizing: border-box;
}
#joinInput{
    width: 25vmax;
    border: none;
    outline: none;
    padding: 1.5vmax;
    font-size: 1.2vmax;
    margin: 2.9vmax;
    box-sizing: border-box;
}
.joinBtn{
    width: 25vmax;
    background-color: crimson;
    color: white;
    font-family: 'Roboto', sans-serif;
    border: none;
    outline: none;
    padding: 1.5vmax;
    font-size: 1.2vmax;
    box-sizing: border-box;
    transition: all 0.3S;
    border-radius: 30PX;
    cursor: pointer;
}
.joinBtn:hover{
    background-color: rgb(145, 30, 53);
    border: 1px solid white;
}
body{
    margin: 0;
}


