.chatPage{
    background-color: black;
    display: flex;
    justify-content: center;
    align-items: center;
    width: 100vw;
    height: 100vh;
}
.chatContainer{
    background-color: white;
    height: 60%;
    width: 50%;
}
.header{
    background-color: crimson;
    height: 15%;
    display: flex;
    align-items: center;
    justify-content: space-between;
}
.header > h2{
    color: white;
    margin: 2.5vmax;
}
.header>a>img{
    margin: 2.5vmax;
    width: 14px;
    transition: all 0.3s ease;
}
.header>a>img:hover{
    -webkit-transform: scale(1.2);
            transform: scale(1.2);
    cursor: pointer;
}
.chatBox{
    height: 70%;
    box-sizing: border-box;
    overflow-y: auto;
}
.inputBox{
    /* border-top: 0.2vmax solid red; */
    height: 15%;
    box-sizing: border-box;
    display: flex;
}
#chatInput{
   
    width: 80%;
    border: none;
    padding: 2vmax;
    box-sizing: border-box;
    background-color: white;
    outline: none;
    font-family: 'Segoe UI', Tahoma, Geneva, Verdana, sans-serif;
    font-size: 1.2vmax;
    border-top: 0.2vmax solid rgba(41, 41, 41, 0.562);
}
.sendBtn{
    color: white;
    background-color: crimson;
    border: none;
    width: 20%;
    transition: all 0.3s;
    font-size: 20px;
    font-family: 'Roboto', sans-serif;
}
.sendBtn > img{
    width: 3vmax;
    -webkit-filter: brightness(100) invert();
            filter: brightness(100) invert();
    transition: all 0.3s ease;
}
.sendBtn:hover{
    background-color: rgb(191, 13, 52);
    border: 1px solid black;
}
.sendBtn:hover img{
    -webkit-transform: translateX(10px);
            transform: translateX(10px);
}
.messageBox{
    padding: 1vmax;
    margin: 1vmax;
    border-radius: 0.5vmax;
    display: inline-block;
    clear: both;
    font-size: 1.2vmax;
    font-family:'Segoe UI', Tahoma, Geneva, Verdana, sans-serif;
}

.right{
    float: right;
    background-color: coral;
    color: white;
    text-align: right;
}

.left{
    float: left;
    text-align: left;
    background-color: rgb(196, 255, 184);
    
}
